import React from 'react';
import type { FC } from 'react';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextControl, ToggleControl } from '@wordpress/components';
import {
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOption as ToggleGroupControlOption,
} from '@wordpress/components';

interface Attributes {
	url: string;
	linkTarget: '_self' | '_blank';
	showSubmenu: boolean;
}

interface Props {
	attributes: Attributes;
	setAttributes: ( newAttributes: Partial< Attributes > ) => void;
}

export const Controls: FC< Props > = ( { attributes, setAttributes } ) => {
	const { url, linkTarget, showSubmenu } = attributes;

	return (
		<InspectorControls>
			<PanelBody>
				<TextControl
					label="リンク先 URL"
					value={ url }
					onChange={ ( url ) => setAttributes( { url } ) }
				/>

				<ToggleGroupControl
					label="リンクターゲット"
					value={ linkTarget }
					onChange={ ( linkTarget ) =>
						setAttributes( { linkTarget } )
					}
					isBlock
				>
					<ToggleGroupControlOption value="_self" label="_self" />
					<ToggleGroupControlOption value="_blank" label="_blank" />
				</ToggleGroupControl>

				<ToggleControl
					label="サブメニューを表示する"
					checked={ showSubmenu }
					onChange={ () =>
						setAttributes( { showSubmenu: ! showSubmenu } )
					}
				/>
			</PanelBody>
		</InspectorControls>
	);
};
